.coming-soon {
    width: 100vw;
    height: 100vh;
    color: white;
    background-color: #0f1b33;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
h1 {
    font-family: "LostFish";
}
body {
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    overflow-y: hidden;
    background-color: #0f1b33;
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: flex-end;
    overflow-x: hidden;
}
.wave1,
.wave2,
.wave3 {
    position: absolute;
    bottom: 0px;
    opacity: 0.8;
    width: 100%;
    flex-direction: column;
}
.waves {
    position: relative;
    width: 100%;
    display: flex;
}
svg {
    transform: translateY(50%) translateX(-5%);
    /* background-color: red; */
    width: 110%;
    height: 100%;
}
